<template>
	<div id="seckill-orderList">
		<public-header title="我的抢购"></public-header>
		<div class="content">
			<div class="navs flex-lr van-hairline--bottom">
				<div class="nav-item" :class="{active: status == 0}" @click="onToggleStatus(0)">
					<div class="nav-title">未支付</div>
				</div>
				<div class="nav-item" :class="{active: status == 1}" @click="onToggleStatus(1)">
					<div class="nav-title">待核销</div>
				</div>
				<div class="nav-item" :class="{active: status == 2}" @click="onToggleStatus(2)">
					<div class="nav-title">已核销</div>
				</div>
				<div class="nav-item" :class="{active: status == 3}" @click="onToggleStatus(3)">
					<div class="nav-title">已取消</div>
				</div>
			</div>
			<van-list  v-model="orders.loading" :finished="orders.loaded" :offset="100" @load="onLoad" :immediate-check="false" v-if="!orders.empty">
				<div class="order-tiem" v-for="(item,index) in orders.data" :key="index">
					<div class="store-info van-hairline--bottom">
						<img :src="item.logo" alt="">
						<div class="title-time">
							<div class="store-title">{{item.store_title}}</div>
							<div class="order-time">下单时间：{{item.addtime_cn}}</div>
						</div>
						<van-icon name='right'></van-icon>
					</div>
					<div class="order-info">
						<img :src="item.thumb" alt="">
						<div class="info-inner">
							<div class="title-price">
								<div class="goods-title">{{item.goods_title}}</div>
								<div class="order-price">{{item.is_pay > 0 ? '实付' : '待付'}}：¥{{item.final_fee}}</div>
							</div>
							<div class="other-info">
								<div>支付方式:</div>
								<div>{{item.pay_type_cn}}</div>
							</div>
							<div class="other-info">
								<div>联系方式:</div>
								<div>{{item.username}}({{item.mobile}})</div>
							</div>
							<div class="other-info" v-if="item.is_pay == 1">
								<div>兑换码:</div>
								<div>{{item.code}}</div>
							</div>
							<div class="other-info" v-if="item.is_pay == 1">
								<div>到期时间:</div>
								<div>{{item.expire_cn}}</div>
							</div>
							<div class="btn-group" v-if="item.status == 1 && item.is_pay == 1">
								<div class="btn-item" @click="onReceive(item.code)">核销取货</div>
								<a class="btn-item" :href="'tel:' + item.telephone">联系商家</a>
							</div>
							<div class="btn-group" v-if="item.status == 1 && item.is_pay == 0">
								<router-link tag="div" :to="util.getUrl({path: '/pages/public/pay', query: {order_id: item.id, order_type: 'seckill'}})" class="btn-item">立即支付</router-link>
							</div>
						</div>
					</div>
				</div>
				<div class="loaded" v-if="orders.loaded">
					<div class="loaded-tips">我是有底线的</div>
				</div>
			</van-list>
			<div class="common-no-con" v-else>
				<img src="/static/img/order_no_con.png" alt="">
				<p>您暂无相关订单哦</p>
			</div>
		</div>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		components: {
			PublicHeader
		},
		data() {
			return {
				showPreLoading: true,
				status: 1,
				orders: {
					page: 1,
					psize: 15,
					loading: false,
					loaded: false,
					empty: false,
					data: []
				}
			}
		},
		methods: {
			onLoad() {
				if (this.orders.loaded) {
					return false;
				}
				this.util.request({
					url: 'seckill/seckill/list',
					data: {
						status: this.status,
						page: this.orders.page,
						psize: this.orders.psize
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.orders.page ++;
					this.orders.data = [...this.orders.data, ...result.message.orders];
					if (!this.orders.data.length) {
						this.orders.empty = true;
					}
					if(result.message.orders.length < this.orders.psize) {
						this.orders.loaded = true;
					}
					this.orders.loading = false;
				})
			},
			onToggleStatus(status) {
				this.status = status;
				this.orders ={
					page: 1,
					psize: 15,
					loading: true,
					loaded: false,
					empty: false,
					data: []
				};
				this.onLoad();
			},
			onReceive(code) {
				this.$dialog.alert({
					message: '您的兑换码是' + code + ', 请将此兑换码出示给店员核销取货'
				});
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#seckill-orderList .content{
		position: fixed;
		top: 46px;
		right: 0;
		left: 0;
		bottom: 0;
		width: 100%;
		background: #f5f5f5;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#seckill-orderList .content::-webkit-scrollbar {
		display: none;
	}
	#seckill-orderList .content .navs{
		width:100%;
		height:44px;
		line-height:44px;
		box-sizing:border-box;
		background-color:#fff;
	}
	#seckill-orderList .content .navs .nav-item{
		width:25%;
		height:100%;
		box-sizing:border-box;
		text-align:center;
		padding:0 20px;
	}
	#seckill-orderList .content .navs .nav-item .nav-title{
		height:100%;
		font-size: 14px;
		box-sizing:border-box;
	}
	#seckill-orderList .content .navs .nav-item.active .nav-title{
		border-bottom:2px solid #ff2d4b;
	}
	#seckill-orderList .content .order-tiem{
		padding:0 10px;
		background-color:#fff;
		margin-top:10px;
		line-height:1;
	}
	#seckill-orderList .content .order-tiem .store-info{
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
		padding:10px 0;
	}
	#seckill-orderList .content .order-tiem .store-info img{
		width:50px;
		height:50px;
		border-radius:3px;
	}
	#seckill-orderList .content .order-tiem .store-info .title-time{
		width:76%;
		height:50px;
		display:flex;
		flex-flow:column nowrap;
		justify-content:space-between;
	}
	#seckill-orderList .content .order-tiem .store-info .title-time .store-title{
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
		font-size:16px;
		width:100%;
	}
	#seckill-orderList .content .order-tiem .store-info .title-time .order-time{
		font-size:13px;
		color:#adadad;
	}
	#seckill-orderList .content .order-tiem .store-info .van-icon-right{
		font-size:18px;
		color:#ccc;
	}
	#seckill-orderList .content .order-tiem .order-info{
		padding:10px 0;
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
	}
	#seckill-orderList .content .order-tiem .order-info img{
		width:24px;
		height:24px;
		border: none;
	}
	#seckill-orderList .content .order-tiem .order-info .info-inner{
		width: 90%;
	}
	#seckill-orderList .content .order-tiem .order-info .info-inner .title-price{
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
		width:100%;
		height:24px;
		line-height:24px;
		margin-bottom:10px;
	}
	#seckill-orderList .content .order-tiem .order-info .info-inner .title-price .goods-title{
		width:50%;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
	}
	#seckill-orderList .content .order-tiem .order-info .info-inner .title-price .order-price{
		font-size:16px;
		font-weight:500;
	}
	#seckill-orderList .content .order-tiem .order-info .info-inner .other-info{
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
		padding:10px 0;
		font-size: 15px;
	}
	#seckill-orderList .content .order-tiem .order-info .info-inner .btn-group{
		padding-top:10px;
		display:flex;
		justify-content:flex-end;
	}
	#seckill-orderList .content .order-tiem .order-info .info-inner .btn-group .btn-item{
		display:inline-block;
		width:85px;
		line-height:1.5;
		border-radius:3px;
		border:1px solid #ccc;
		text-align:center;
		font-size:14px;
		color: #000;
		padding:5px 0;
		margin-left:10px;
	}
	#seckill-orderList .content .common-no-con{
		position: absolute;
		top: 32%;
		left: 0;
		width: 100%;
		height: 200px;
		text-align: center;
		color: #768491;
		font-size: 16px;
	}
	#seckill-orderList .content .common-no-con img{
		max-width: 25%;
		vertical-align: middle;
		border: none;
	}
	#seckill-orderList .content .common-no-con p{
		margin: 20px 0;
		line-height: 24px;
	}
</style>


